* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding: 0;
  margin: 0;
}

.country-list_item {
  display: flex;
  align-items: center;

  margin: 0 0 20px 0;
}

.country-list_item img {
  width: 90px;
  height: auto;
  margin-right: 10px;
}

.country-list_item p {
  font-size: 20px;
  font-weight: 700;
  margin: 0;
}
